"use client";

import { useParams, useRouter } from "next/navigation";
import { useQuery } from "convex/react";
import { ArrowLeft, Loader2, MoveRight, Shield, Sparkles, Truck } from "lucide-react";
import { ThemeToggle } from "@/components/ThemeToggle";
import { formatCurrency } from "@/lib/format";
import type { PublicProduct } from "@/lib/types";

export default function ProductPage() {
  const params = useParams();
  const router = useRouter();
  const productId = params?.id as string;

  const product = useQuery("products:getPublic", { id: productId }) as PublicProduct | null | undefined;
  const isLoading = product === undefined;

  if (isLoading) {
    return (
      <div className="stack">
        <div className="topbar">
          <button className="button" onClick={() => router.push("/")}>
            <ArrowLeft size={16} />
            Nazad
          </button>
          <ThemeToggle />
        </div>
        <div className="loading" style={{ marginTop: 12 }}>
          <Loader2 className="animate-spin" size={18} />
          Učitavanje proizvoda...
        </div>
      </div>
    );
  }

  if (!product) {
    return (
      <div className="stack">
        <div className="topbar">
          <button className="button" onClick={() => router.push("/")}>
            <ArrowLeft size={16} />
            Nazad
          </button>
          <ThemeToggle />
        </div>
        <div className="empty" style={{ marginTop: 12 }}>
          Proizvod nije pronađen.
        </div>
      </div>
    );
  }

  const heroImage = product.images?.find((img) => img.isMain) ?? product.images?.[0];

  return (
    <div className="detail-shell">
      <div className="topbar">
        <button className="button" onClick={() => router.push("/")}>
          <ArrowLeft size={16} />
          Nazad
        </button>
        <ThemeToggle />
      </div>

      <div className="detail-grid">
        <div className="glass hero-card">
          <div className="eyebrow">Proizvod</div>
          <h2 className="detail-title">{product.name}</h2>
          <p className="muted">{product.opis ?? product.opisFbInsta ?? product.opisKp ?? "Detalji na upit."}</p>
          <div className="detail-actions">
            <div className="pill strong">{formatCurrency(product.prodajnaCena)}</div>
            <button className="button primary">
              Naruči <MoveRight size={16} />
            </button>
            <button className="button" onClick={() => window.scrollTo({ top: document.body.scrollHeight, behavior: "smooth" })}>
              Pogledaj dodatno
            </button>
          </div>
          <div className="meta-row" style={{ marginTop: 12 }}>
            <span className="badge">
              <Sparkles size={14} />
              Proveren alat
            </span>
            <span className="badge">
              <Truck size={14} />
              Brza isporuka
            </span>
            <span className="badge">
              <Shield size={14} />
              Sigurna kupovina
            </span>
          </div>
          <div style={{ marginTop: 16 }}>
            {heroImage?.url ? (
              // eslint-disable-next-line @next/next/no-img-element
              <img src={heroImage.url} alt={product.name} className="hero-img" />
            ) : (
              <div className="product-card__placeholder" style={{ height: 260, borderRadius: 16, border: "1px dashed var(--border)" }}>
                Bez slike
              </div>
            )}
          </div>
        </div>

        <div className="glass hero-card">
          <div className="detail-top">
            <div>
              <p className="eyebrow">Varijante i cene</p>
              <h3 className="detail-subtitle">Izaberi ono što ti odgovara</h3>
            </div>
            <span className="pill">{product.variants?.length ?? 0} var.</span>
          </div>

          {product.variants && product.variants.length > 0 ? (
            <div className="variant-list">
              {product.variants.map((variant) => (
                <div key={variant.id} className="variant-card">
                  <div className="variant-card__header">
                    <div>
                      <p className="eyebrow">{variant.isDefault ? "Glavni izbor" : "Varijanta"}</p>
                      <h4 style={{ margin: "4px 0 2px" }}>{variant.label}</h4>
                    </div>
                    <div className="price">{formatCurrency(variant.prodajnaCena)}</div>
                  </div>
                  {variant.opis && <p className="muted" style={{ marginTop: 6 }}>{variant.opis}</p>}
                  {variant.images && variant.images.length > 0 && (
                    <div className="thumb-row">
                      {variant.images.slice(0, 4).map((image) =>
                        image.url ? (
                          // eslint-disable-next-line @next/next/no-img-element
                          <img
                            key={image.storageId}
                            src={image.url}
                            alt={variant.label}
                            className="thumb"
                            loading="lazy"
                          />
                        ) : null,
                      )}
                    </div>
                  )}
                </div>
              ))}
            </div>
          ) : (
            <p className="muted">Ovaj proizvod nema dodatne varijante.</p>
          )}
        </div>
      </div>

      <div className="glass hero-card" style={{ marginTop: 10 }}>
        <div className="detail-top">
          <div>
            <p className="eyebrow">Kako poručiti</p>
            <h3 className="detail-subtitle">Brza kupovina u tri koraka</h3>
          </div>
        </div>
        <div className="steps">
          <div className="step">
            <span className="step-index">1</span>
            <div>
              <p className="highlight-title">Izaberi model</p>
              <p className="muted">Pogledaj slike i specifikaciju varijante koja ti treba.</p>
            </div>
          </div>
          <div className="step">
            <span className="step-index">2</span>
            <div>
              <p className="highlight-title">Pošalji upit</p>
              <p className="muted">Klik na “Naruči” i ostavi podatke za isporuku (telefon/adresa).</p>
            </div>
          </div>
          <div className="step">
            <span className="step-index">3</span>
            <div>
              <p className="highlight-title">Preuzmi</p>
              <p className="muted">Potvrđujemo dostupnost i šaljemo isti dan gde god treba.</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
